* {
  box-sizing: border-box;
}

body {
  padding-top: 50px;
  padding-left: 50px;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

/* ====================================================== */

ul{
  list-style: none;
}

#search-box {
font-size: 20px;
  width: 500px;
  font-weight: 700;

}
#search-box:focus {
border-color : blue;
}

.country-list__info-item{
  display: flex;

}

.country-list__info-name{
  font-size: 50px;
  font-weight: 900;
  margin: 0;
  margin-left: 10px;

}

.country-info__list{
 padding-left: 10px;
 margin-top: 0;
}

.country-info__item{
  display: flex;
}
.country-info__text{
  font-size: 20px;
  font-weight: 700;
}
.country-info__text-value{
   margin-left: 10px;
  font-size: 20px;
  font-weight: 500;
}

.country-list{
  padding-left: 10px;
  margin-top: 0;
}

.country-list__item{
  display: flex;
  align-items: center;
}
.country-list__name{
margin: 10px;
  margin-right: 0;
  font-size: 30px;
  font-weight: 700;
}
